.header {
    position: relative;
    padding-top: 3rem;
    .header-content {
        position: relative;
        padding-top: 4rem;
        min-height: 100vh;
    }
    .headline {
        position: relative;
        display: block;
        font-size: 46px;
        font-weight: 700;
        text-transform: capitalize;
        font-family: "Playfair Display", serif;
    }
    .header-image {
        position: relative;
        width: 100%;
    }
}
.section{
    position: relative;
    padding: 2rem 0;
    .card-stunting{
        --bs-card-color: #1e1f1e;
        .card-body{
            height: 320px;
            overflow: hidden;
            transition: all 1s ease-in-out;
            &.show-more{
                height: unset;
            }
        }
        a#readmore{
            &::before{
                content: "Lihat Lebih";
            }
            &.active{
                &::before{
                    content: "Tutup";
                }
            }
        }
    }
}

footer{
    position: relative;
    padding: 6rem 0 2rem;
    .social-media{
        margin-bottom: 1rem;
        i{
            font-size: 24px;
        }
    }
}

@media (max-width:992px){
    footer{
        padding: 5rem 0 1rem;
    }
}