@import "navbar", "landing", "about",'diagnosis';

:root{
    --bs-body-color: #1e1f1e;
    --bg-body: #f0ffee;
}

body {
    background-color: var(--bg-body);
}

.background-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    .dots {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        .dot {
            position: absolute;
            aspect-ratio: 1;
            border-radius: 300px;
            opacity: 0.5;
            &:nth-child(1) {
                top: 5%;
                left: 5%;
                width: 25%;
                background: radial-gradient(
                    circle,
                    #8bff9b 50%,
                    transparent 90%
                );
                box-shadow: 0 0 3rem #8bff9b;
            }
            &:nth-child(2) {
                top: 0;
                left: 60%;
                width: 35%;
                background: radial-gradient(
                    circle,
                    #52ffb7 50%,
                    transparent 90%
                );
                box-shadow: 0 0 3rem #52ffb7;
            }
            &:nth-child(3) {
                top: 40%;
                left: 40%;
                width: 25%;
                background: radial-gradient(
                    circle,
                    #76ddff 50%,
                    transparent 90%
                );
                box-shadow: 0 0 3rem #76ddff;
            }
            &:nth-child(4) {
                bottom: 10%;
                left: 30%;
                width: 15%;
                background: radial-gradient(
                    circle,
                    #89ee97 50%,
                    transparent 90%
                );
                box-shadow: 0 0 3rem #89ee97;
            }
            &:nth-child(5) {
                bottom: 5%;
                right: 0%;
                width: 20%;
                background: radial-gradient(
                    circle,
                    #a0fffc 50%,
                    transparent 90%
                );
                box-shadow: 0 0 3rem #a0fffc;
            }
            &:nth-child(6) {
                bottom: 0;
                left: 0;
                width: 25%;
                background: radial-gradient(
                    circle,
                    #a9e9ff 50%,
                    transparent 90%
                );
                box-shadow: 0 0 3rem #a9e9ff;
            }
        }
    }
    .bg {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: #dbf5d2;
        opacity: 0.8;
        backdrop-filter: blur(10px);
    }
    .wave {
        position: absolute;
        left: 0;
        width: 100%;
        &.wave-bottom {
            bottom: 0;
            transform: translateY(10%) scaleX(-1);
        }
        &.wave-top {
            top: 0;
            transform: translateY(-10%) scaleY(-1);
        }
        path{
            fill: var(--bg-body);
        }
    }
}

.page-content {
    position: relative;
}

.scrolled {
    background-color: rgba($color: #c1e4b5, $alpha: 0.8) !important;
    backdrop-filter: blur(10px);
    box-shadow: 0 3px 1rem rgba($color: #000000, $alpha: 0.05);
}

.btn {
    padding: 0.5rem 2rem;
}
