.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1020;
    transition: all 0.5s ease;
    padding: .5rem 0;
    .navbar-brand{
        img{
            height: auto!important;
        }
    }
    .navbar-nav {
        .nav-link {
            font-size: 16px;
            font-weight: 600;
            &.active{
                color: var(--bs-success);
            }
        }
    }
}
